html,
body {
  margin: 0;
  padding: 0;
}

#one-click {
  background: white;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: none;
}

#one-click:has(.MuiSlider-dragging) {
  cursor: grabbing;
}
/* TODO: remove temp style when pseudo platform bar is no longer needed */
#pseudo-platform-bar {
  height: 75px;
  width: 100%;
  background: #305366;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

#pseudo-platform-bar .status {
  padding: 25px 35px;
  font-size: 22px;
  font-weight: 500;
  font-family: "Roboto Mono";
}

#remote-status {
  color: #305366;
  text-align: left;
}

#remote-status.warning {
  color: #ff9e69;
}

#player-status {
  color: white;
  text-align: right;
}

#main-view {
  flex: 1;
  position: relative;
  display: flex;
}

#slides-container {
  flex: 1;
  padding: 0;
  display: flex;
  justify-content: center;
}

#slide-deck {
  height: 100%;
  flex: 1;
}

#curr-poll-indicator-container {
  --ratio: calc(16/9);
  height: min(100%, calc(100vw / var(--ratio)));
  max-width: 100%;
  aspect-ratio: var(--ratio);
  position: absolute;
  z-index: 1;
  max-height: 720px;
  align-self: center;
  container-type: size;
}

#curr-poll-number-completed {
  font-size: 8cqh;
  text-align: center;
  width: 10cqh;
  float: right;
  font-weight: 700;
  letter-spacing: -3px;
  transform: translate(-6.5cqh, 2cqh) rotate(10deg);
}

.progress-bars {
  padding: 0px;
  z-index: 2;
  overflow-x: clip;
}

.slides-controls {
  margin-bottom: 0;
  padding: 0;
}

.curr-slide-num-label {
  padding: 12px 0px;
  text-align: center;
  align-self: stretch;
  width: 30vw;
  border-radius: 30px;
  background-color: white;
  display: flex;
}

.curr-slide-num-label > p {
  margin: auto;
  font-size: 22px;
  font-weight: bold;
  font-family: "Roboto Mono";
}

.one-click-ctrl-bar {
  background-color: #b8b8b8;
  padding: 15px 30px;
}

.ctrl-move-slide-btn:focus:not(:focus-visible) {
  border: none;
  background-color: white;
}

.ctrl-move-slide-btn-label {
  margin: 0;
  color: black;
  font-size: 16px;
  font-weight: 500;
  font-family: "Roboto Mono";
  width: 100%;
}

.ctrl-btn-icon {
  color: black;
  border-radius: 20px;
}

.tailspin-loader {
  align-items: center;
  justify-content: center;
  min-width: 100%;
  min-height: 100%;
  background-color: rgb(20, 20, 20);
}

#poll-graph-container {
  background: white;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.poll-btns-container {
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
}

.poll-btns-title {
  flex: 0 0 100%;
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 16px;
}
